/* radio 24x24*/
.input_rdo2 {display:inline-block; position:relative;}
.input_rdo2 input[type="radio"] {position:absolute; top:0; left:0; margin:0; cursor:pointer; opacity:0;}
.input_rdo2 input[type="radio"] + label {display:block; position:relative; height:24px; padding-left:29px; line-height:24px;}
.input_rdo2 input[type="radio"]:not(:checked) + label:before,
.input_rdo2 input[type="radio"]:checked + label:before {
    content:''; 
    position:absolute; 
    top:50%; 
    left:0; 
    width:24px; 
    height:24px; 
    line-height:24px; 
    margin-top:-12px; 
    background:url(../../../static/images/mobile/bg_radio.png) no-repeat 0 0; 
    background-size:24px 48px;}
.input_rdo2 input[type="radio"]:checked + label:before {background-position:0 bottom;}
.input_rdo2 input[type="radio"]:disabled + label:before {
    content:''; 
    position:absolute; 
    top:50%; 
    left:0; 
    width:24px; 
    height:24px; 
    line-height:24px; 
    margin-top:-12px; 
    background:url(../../../static/images/mobile/bg_radio_disabled.png) no-repeat 0 0; 
    background-size:24px 48px;}
.input_rdo2 input[type="radio"]:checked:disabled + label:before {background-position:0 bottom;}
.input_rdo2 input[type="radio"]:not(:checked):focus + label:before,
.input_rdo2 input[type="radio"]:checked:focus + label:before {outline:1px dotted #000;}
