.ellipsis {
    display:block;
    overflow:hidden;
    white-space:nowrap;
    text-overflow:ellipsis
}
.Header {
    display: block;
    background: #495057;
    background-color:#ffff;
    width:100%; 
    height:69px; 
    border-bottom:1px solid #cacaca; 
    position:relative;


    h1{
        font-size:18px; 
        font-family:'Noto Sans KR';
        font-weight:500; 
        color:#000; 
        width:100%; 
        text-align:left; 
        padding:25px 42px 0 42px;


    }

    
    .logo{
        position:absolute;
        left:15px;
        top:25px;
        img {
            width:20px;
        }
    }




    .btn_close {
        position:absolute; 
        right:15px; 
        top:25px;
        img {
            width:20px;
        }
    }
}