/* btn_gray_b1 */
.btn_gray_b1 button ,
.btn_gray_b1 input ,
.btn_gray_b1
 a {width:100%; height:50px; line-height:50px; font-size:15px; font-family:'Noto Sans KR'; font-weight:500; color:#fff; background:#777;}
.btn_gray_b1 a:link,
.btn_gray_b1 a:active,
.btn_gray_b1 a:visited,
.btn_gray_b1 a:focus,
.btn_gray_b1 a:hover {color:#fff;}
.btn_gray_b1.line2 a {line-height:20px; padding:3px 0px 0px 0px;}

