@import url(https://fonts.googleapis.com/earlyaccess/notosanskr.css); 

html, body {width:100%;height:100%;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;text-size-adjust:none}
html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, a, button, address, em, img, small, strong, sub, sup, dl, dt, dd, ol, ul, li, fieldset, form, label, legend,table, caption, tbody, tfoot, thead, tr, th, td, hr, figure {margin:0;padding:0;-ms-word-break:keep-all;word-break:keep-all;-webkit-box-sizing:border-box;box-sizing:border-box;}
body {padding:0;background:#fff; font-family:'Noto Sans KR', dotum, '돋움', gulim, '굴림', Arial, AppleGothic, verdana, sans-serif, helvetica; font-size:15px; color:#484848; line-height:1.2;}
h1, h2, h3, h4, h5, h6 {font-weight:normal}
ol, ul, li {list-style:none}
table {width:100%;border-collapse:collapse;border-spacing:0}
form, fieldset, iframe {display:block;border:0}
img, button {border:0 none;vertical-align:top;}
hr {height:0;display:none}
i, em, address {font-style:normal}
label, button {cursor:pointer}
caption, legend, .blind {width:0;height:0;margin:0;padding:0;text-indent:-9999em;overflow:hidden;visibility:hidden;font-size:0}
.blind {position:absolute !important}
header, footer, section, article, aside, nav, hgroup, details, menu, figure, figcaption {display:block}
header {background-color:#fff}
input, textarea, select  {-webkit-appearance:none;margin:0;padding:0;-webkit-border-radius:0;border-radius:0;vertical-align:middle;outline-style:none}
select::-ms-expand{display:none}
textarea {-webkit-box-sizing:border-box;box-sizing:border-box;padding:20px 12px;width:100%;border:1px solid #e5e5e5;font-size:15px;font-family:'Noto Sans KR', dotum,'돋움',gulim,'굴림',Arial, AppleGothic, verdana, sans-serif, helvetica}
button {overflow:visible;margin:0;padding:0;border:none;background:none;font-size:0;font-family:'Noto Sans KR', dotum,'돋움',gulim,'굴림',Arial, AppleGothic, verdana, sans-serif, helvetica;vertical-align:top;cursor:pointer}
button::-moz-focus-inner {border:0;padding:0}
.ellipsis {display:block;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}
a {color:#111;text-decoration:none;word-wrap:break-word;word-break:keep-all}
a:link, a:visited, a:hover, a:active, a:focus {text-decoration:none}
.blind {display:inline-block;width:0;font-size:0;height:0;text-indent:-99999px}

/* placeholder */
input::placeholder {color:#999}
input::-webkit-input-placeholder {color:#999}
input::-ms-input-placeholder {color:#999}
textarea::placeholder {color:#999}
textarea::-webkit-input-placeholder {color:#999}
textarea::-ms-input-placeholder {color:#999}

.AppLayout {
    width:100%;
    background:#f6f6f6;
}

