/* btn default */
span.timerbtn { /*position:relative; */ width:100%; display:inline-block; overflow:visible; margin:0; text-decoration:none !important; white-space:nowrap; cursor:pointer; vertical-align:middle; text-align:center;}
.timerbtn input { /*position:relative; */ display:-moz-inline-stack; display:inline-block; overflow:visible; border:0; margin:0; text-decoration:none !important; white-space:nowrap; cursor:pointer;}
.timerbtn button {display:inline-block;}
.timerbtn a {display:inline-block;}
.timerbtn *:hover,
.timerbtn *:focus {text-decoration:none;}

/* btn_white_s1 */
.btn_white_s1 button ,
.btn_white_s1 input ,
.btn_white_s1 a {width:100%; height:40px; line-height:40px; font-size:15px; font-family:'Noto Sans KR'; font-weight:500; color:#484848; background:#ff12;}
.btn_white_s1 a:link,
.btn_white_s1 a:active,
.btn_white_s1 a:visited,
.btn_white_s1 a:focus,
.btn_white_s1 a:hover {color:#484848;}