#UserGuide{
    background:#fff; padding:0 15px;
    ol, ul, li {list-style:none}

    > a {
        display:block; 
        height:50px; 
        line-height:50px; 
        font-size:15px; 
        color:#000; 
        font-family:'Noto Sans KR';
        font-weight:500;
        background:url(../../static/images/mobile/btn_arr_open.png) no-repeat right center; 
        background-size:20px 11px;
    }

    .open {
        background:url(../../static/images/mobile/btn_arr_close.png) no-repeat right center; 
        background-size:20px 11px;
    }

    .bu01 {
        li {
            padding-left:10px; 
            margin-top:7px; 
            background:url(../../static/images/mobile/bu_black01.gif) no-repeat 0 7px; 
            font-size:15px; line-height:18px; 
            color:#484848; 
            font-family:'Noto Sans KR'; 
            font-weight:400;
        }
        :first-child {
            margin-top:0px;
        }

    }

    .cont {
        background:#fff; 
        width:100%; 
        padding:20px 0px; 
        border-top:1px dashed #ddd;


        .step_guide {
            margin-top:10px;
            border:1px solid #ddd;
            .list {
                padding:0 15px;
                li {
                    background:url(../../static/images/mobile/bg_arr.gif) no-repeat top center; 
                    font-size:15px; 
                    line-height:18px; 
                    color:#484848; 
                    font-family:'Noto Sans KR'; 
                    font-weight:500;
                    :first-child{ 
                        background:none;
                    } 
                }

                p {
                    vertical-align: middle; 
                    padding:15px 0 15px 65px; 
                    min-height:70px;
                }

                p.bg01 {
                    background:url(../../static/images/mobile/img_step01.png) no-repeat 0 center; 
                    background-size:55px 55px;
                }
                p.bg02 {
                    background:url(../../static/images/mobile/img_step02.png) no-repeat 0 center; 
                    background-size:55px 55px;
                }
                p.bg03 {
                    background:url(../../static/images/mobile/img_step03.png) no-repeat 0 center; 
                    background-size:55px 55px;
                }
                p.bg04 {
                    background:url(../../static/images/mobile/img_step04.png) no-repeat 0 center; 
                    background-size:55px 55px;
                }
                p.bg05 {
                    background:url(../../static/images/mobile/img_step05.png) no-repeat 0 center; 
                    background-size:55px 55px;
                }



            }

        }

    }
}